$font-path: "../fonts";

@font-face {
  font-family: "iconfont";
  src: url("#{$font-path}/iconfont/iconfont.ttf?xb8o7r") format("truetype"),
    url("#{$font-path}/iconfont/iconfont.woff?xb8o7r") format("woff"),
    url("#{$font-path}/iconfont/iconfont.svg?xb8o7r#iconfont") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"]::before,
[class*=" icon-"]::before {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "iconfont" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

